.app {
  height: 100vh;
  width: 100vw;
  background-color: #df75a0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Inter', sans-serif;
}

.header {
  font-family: 'League Script', cursive;
  font-size: 5rem;
}

.content {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}

.content-container {
  height: 50vh;
  width: 40vw;
  background-color: #f9dbe6;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.button {
  width: 90%;
}

.input {
  width: 90%;
}
